.container{
    padding: 10px;
}
.contactInfo{
    display: flex;
    @media (max-width: 576px) {
        flex-direction: column;
      }
}
.orderContent{
    width: 70%;
    @media (max-width: 576px) {
        width: 100%;
      }
}
.orderConfirmContainer{
    width: 30%;
    position: relative;
    padding: 10px 10px;
    @media (max-width: 576px) {
        width: 100%;
      }
}
.orderConfirm{
    position: fixed;
    background-color: whitesmoke;
    text-align: center;
    @media (max-width: 576px) {
        position: relative;
      }
}